import { createRouter, createWebHashHistory, RouteRecordRaw } from "vue-router";

const staticRouteMap: Array<RouteRecordRaw> = [
  {
    path: "/",
    name: "layout",
    meta: {
      title: "TaoYao",
      index: 1,
    },
    component: () => import("../views/Layout/index.vue"),
  },
  {
    path: "/author",
    name: "author",
    meta: {
      title: "作者",
      index: 2,
    },
    component: () => import("../views/author/index.vue"),
  },
  {
    path: "/music",
    name: "music",
    meta: {
      title: "喜欢",
      index: 3,
    },
    component: () => import("../views/music/index.vue"),
  },
  {
    path: "/course",
    name: "course",
    meta: {
      title: "历程",
      index: 4,
    },
    component: () => import("../views/course/index.vue"),
  },
  {
    path: "/dynamic",
    name: "dynamic",
    meta: {
      title: "问答",
      index: 5,
    },
    component: () => import("../views/dynamic/index.vue"),
  },
  {
    path: "/card",
    name: "card",
    meta: {
      title: "学习",
      index: 6,
    },
    component: () => import("../views/card/index.vue"),
  },
  {
    path: "/Thank",
    name: "Thank",
    meta: {
      title: "more",
      index: 7,
    },
    component: () => import("../views/Thank/index.vue"),
  },
  {
    path: "/error",
    name: "error",
    meta: {
      title: "error",
      index: 8,
    },
    component: () => import("../views/phone/index.vue"),
  },
  {
    path: "/:pathMatch(.*)",
    redirect: "/",
  },
];

const routes: Array<RouteRecordRaw> = [...staticRouteMap];

const router = createRouter({
  history: createWebHashHistory(),
  routes,
});
router.beforeEach((to, from, next) => {
  const bool = /Android|webOS|iPhone|iPod|BlackBerry/i.test(
    navigator.userAgent
  );
  if (bool) {
    if (to.path == "/error") {
      next();
    } else {
      router.replace("/error");
    }
  } else {
    if (to.path == "/error") {
      router.replace("/");
    } else {
      next();
    }
  }
});

export default router;
